.App {
  width: 100vw;
  height: 100vh;
  background-color: lightblue;
}

.Fader {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 1);
  transition: background 4s;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &.fading {
    background-color: rgba(255, 255, 255, 1);
  }

  &.revealing {
    transition: background 4s;
    background-color: rgba(255, 255, 255, 0);
  }

  &.revealed {
    display: none;
  }

  .ticketImage {
    width: 75%;
    max-width: 620px;
    height: 75%;
    max-height: 496px;
    object-fit: contain;
    -webkit-filter: drop-shadow(40px 20px 54px rgba(255, 255, 255, 0.68));
    filter: drop-shadow(40px 20px 54px rgba(255, 255, 255, 0.68));
    cursor: pointer;

    opacity: 0;
    transition: opacity 2s ease-in-out;

    &.revealing {
      opacity: 1;
    }
  }
}

.MainBody {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-image: url("../img/space.jpg");
  background-repeat: repeat-x;
  background-color: rgb(2, 2, 2);

  animation: slide 90s linear infinite;

  @keyframes slide {
    from {
      background-position: 0 0;
    }

    to {
      background-position: -1120px 0;
    }
  }

  .topComponents {
    flex: 0 0 70%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .bottomComponents {
    overflow: hidden;
    flex: 0 0 30%;
    width: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 0%;

      transition: top 15s ease-out;
      position: relative;
      top: 100%;

      &.moveIn {
        top: 0%;
      }
    }
  }
}